@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Montserrat'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  min-height: 100%;
  position: relative;
}

.App-header {
  background-color: white;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: #444444;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.content {
  padding: 1%;
  padding-bottom: 40px
}
.navbar {
    min-height: 10vmin;
    background-color: white;
}

.navbar #basic-navbar-nav>li>a {
    color: #444444;
}

.logo {
    font-weight: 500;
}
.site-footer {
	background-color: #f1f1f1;
	color: #444444;
	width: 100%;
	bottom: 0;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	font-size:15px;
	padding:45px 0 20px;
	line-height:24px;
}
.row {
	display: flex;
	justify-content: center;
	align-items: flex-start;
}
.site-footer hr
{
  border-top-color:#444444;
  opacity:0.3
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#eda1aa;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a:link, .site-footer a:visited
{
  color: #444444;
}
.site-footer a:hover, .site-footer a:active
{
  color:#eda1aa;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#f1dede
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
/*.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:white;
  font-weight:700;
  font-size:13px
}*/
.site-footer .social-icons a{
  background-color:#444444;
  color:#f1f1f1;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  transition:all .2s linear
}

.site-footer .social-icons a:hover{
	background-color: white;
}
.site-footer .social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}

@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}

.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.instagram:hover
{
  background-color:#e1306c
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}

